.expense-date {
  display: flex;
  flex-direction: column;
  width: 6.5rem;
  height: 6.5rem;
  border: 1px solid #ececec;
  background-color: rgb(31, 31, 31);
  color: white;
  border-radius: 12px;
  align-items: center;
  justify-content: center;
}

.expense-date__month {
  font-size: 0.85rem;
  font-weight: bold;
}

.expense-date__year {
  font-size: 0.85rem;
}

.expense-date__day {
  font-size: 1.8rem;
  font-weight: bold;
}
